/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1400px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

.main {
  margin-top: 3rem;
}
.main::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 10rem;
  background-color: #454955;
  bottom: 0;
  z-index: -100;
}
@media (min-width: 768px) {
  .main {
    margin-top: 10rem;
  }
}
@media (min-width: 768px) {
  .main__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.main__finten {
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .main__finten {
    padding-bottom: 0;
  }
}
.main__imagen {
  width: 25rem;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .main__imagen {
    margin: 0;
  }
}
.main__texto {
  font-weight: 400;
  font-size: 1.4rem;
  text-align: center;
}
@media (min-width: 1024px) {
  .main__texto {
    text-align: left;
  }
}

.formulario {
  background-color: #000000;
  padding: 1rem 2rem;
  color: #e1e1e1;
  text-align: center;
  width: 80%;
  margin: 0 auto;
  border-radius: 0.3rem;
}
.formulario__descripcion {
  font-size: 1.4rem;
}
.formulario__registrate {
  color: #00b4d8;
}
.formulario__noCuenta, .formulario__registrate, .formulario__avisoPrivacidad, .formulario__terminosCondiciones {
  font-size: 1.4rem;
}

.login {
  display: flex;
  flex-direction: column;
}
.login__usuario, .login__contraseña {
  text-align: left;
}
.login__recuperar {
  color: #00b4d8;
  text-align: right;
}
.login input:not([type=submit]) {
  padding: 1rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
  outline: none;
  border: 3px solid transparent;
}
.login input:not([type=submit]):focus {
  border: 3px solid #00b4d8;
}
.login__recuperar {
  margin-bottom: 2rem;
}
.login input[type=submit] {
  padding: 0.8rem;
  width: 80%;
  margin: 0 auto;
  text-transform: uppercase;
  font-size: 1.4rem;
  background-color: #00b4d8;
  border: none;
  color: #fff;
  transition-property: background-color color;
  transition-duration: 0.3s;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .login input[type=submit] {
    width: 50%;
  }
}
.login input[type=submit]:hover {
  background-color: #fff;
  color: #00b4d8;
}